export default "\n@import url(\"https://netdna.bootstrapcdn.com/font-awesome/4.2.0/css/font-awesome.css\");\n@charset \"UTF-8\";\n.container {\n  background-color: #f6f6f6;\n  min-height: 80vh;\n}\n*,\n*:before,\n*:after {\n  box-sizing: border-box;\n}\nhtml,\nbody {\n  height: 100%;\n  font: 16px/1 \"Open Sans\", sans-serif;\n  color: #555;\n  background: #e5e5e5;\n}\nbody {\n  padding: 50px;\n}\n.accordion {\n  transform: translateZ(0);\n  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);\n  background: #fff;\n}\n.accordion > .accordion-toggle {\n  position: absolute;\n  opacity: 0;\n  display: none;\n}\n.accordion > label {\n  position: relative;\n  display: block;\n  height: 50px;\n  line-height: 50px;\n  padding: 0 40px;\n  font-size: 20px;\n  font-weight: 500;\n  padding-bottom: 70px;\n  background: #fff;\n  color: lightslategrey;\n  cursor: pointer;\n  border-bottom: 1px solid #e6e6e6;\n}\n.first,\n.second {\n  padding-top: 30px;\n}\nlabel {\n  margin-bottom: 0px !important;\n}\n.accordion > label:after {\n  content: \"\";\n  color: #5499da;\n  position: absolute;\n  top: 0px;\n  right: 40px;\n  font-family: fontawesome;\n  transform: rotate(180deg);\n  transition: 0.3s transform;\n}\nsection {\n  padding-left: 10px;\n}\n.accordion > section {\n  height: 0;\n  transition: 0.3s all;\n  overflow: hidden;\n}\n.accordion > .accordion-toggle:checked ~ label:after {\n  transform: rotate(0deg);\n}\n.accordion > .accordion-toggle:checked ~ section {\n  height: 200px;\n}\n.second > .accordion-toggle:checked ~ section {\n  height: 300px;\n}\n.accordion > section p {\n  margin: 15px 0;\n  padding: 0 40px;\n  line-height: 1.5;\n}\n.label {\n  text-transform: uppercase;\n  letter-spacing: 2px;\n  color: #c7c7c7;\n}\n.detail {\n  color: #707070;\n}\n/* Create two equal columns that floats next to each other */\n.column {\n  float: left;\n  width: 50%;\n  padding: 30px;\n}\n/* Clear floats after the columns */\n.row:after {\n  content: \"\";\n  display: table;\n  clear: both;\n}\n.